<template>
  <div class="popular-playlist">
    <h3>Playlists</h3>
    <div class="popular">
      <span v-if="loading">Загрузка...</span>
      <playlistItem 
        v-else 
        v-for="playlist in playlists" 
        :key="playlist.id" 
        :id="playlist.id"
        :playlist="playlist"
      />
    </div>
  </div>
</template>

<script>
import playlistItem from '@/components/PlaylistItem/index.vue'

import { getPlaylists } from '@/api/playlists';

export default {
  data(){
    return{
      playlists: [],
      loading: false
    }
  },
  async mounted() {
    this.loading = true

    const response = await getPlaylists()
    this.playlists = response.data.data

    this.loading = false
  },
  components: {
    playlistItem
  },
  watch: {
    async '$route'() {
      this.loading = true

      const response = await getPlaylists()
      this.playlists = response.data.data

      this.loading = false
    } 
  }
}

