import Vue from 'vue'
import Vuex from 'vuex'
import user from './user'
import app from './app'
import permission from './permission'
import getVersion from '@/utils/getVersion'

Vue.use(Vuex)
export default new Vuex.Store({
  modules: {
    app,
    user,
    permission
  },
  getters: {
    permission_routers: state => state.permission.routers,
    version: () => getVersion()
  }
})
