<template>
  <div class="genre">
    <el-form ref="form" :model="form" label-width="120px" class="genre-add">
      <el-form-item label="Жанр" class="genre-add-label">
        <el-input v-model="form.genre" class="genre-add-input" clearable></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleSubmit" :disabled="loadingbtn" :loading="loadingbtn" class="genre-add-btn">Добавить</el-button>
      </el-form-item>
    </el-form>
    <table
      class="genre-table"
      v-loading="loading">
      <tr>
        <th class="id">id</th>
        <th>Название</th>
        <th></th>
      </tr>
      <tr v-for="({ id, name }) in genres" :key="id">
        <td class="id">{{ id }}</td>
        <td>{{ name }}</td>
        <td>
          <el-button @click="e => handleClick(e, id)" :disabled="genreLoading" icon="el-icon-delete" type="danger" plain></el-button>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import { getGenres, addGenre, deleteGenre } from '@/api/file'

export default {
  data() {
    return {
      form: {
        genre: ''
      },
      genres: [],
      loadingbtn: false,
      loading: true,
      genreLoading: false
    }
  },
  async mounted() {
    const response = await getGenres(15, 1)
    this.genres = response.data.data
    this.loading = false
  },
  methods: {
    async handleClick(e, id) {
      this.$confirm('Вы точно хотите удалить этот жанр?', 'Warning',{
        confirmButtonText: 'Удалить',
        cancelButtonText: 'Закрыть',
        type: 'warning'
      }).then(async () => {
        this.genreLoading = true
        await deleteGenre(id)
        const response = await getGenres(15, 1)
        this.genres = response.data.data
        this.genreLoading = false
        this.$message({
          type: 'success',
          message: 'Жанр удален'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: 'Вы отменили удаление'
        })
      })
    },
    async handleSubmit() {
      const { genre } = this.form

      if(!genre) {
        this.$message.error('Введите название жанра')
        return false
      }

      this.loadingbtn = true

      await addGenre({ name: genre })
      const response = await getGenres(15, 1)
      this.genres = response.data.data

      this.loadingbtn = false
    }
  }
}
</script>


