<template>
  <div>
    <modal name="RegistationModal" scrollable classes="modal" width="500px" height="auto">
      <div class="modal__body" style="background: #fff !important; padding: 30px;">
        <h3><mark>My</mark>TopMusic</h3>
        <p>Зарегестрироваться на сайте</p>
        <el-form
          :model="registerData"
          ref="registerForm">
            <el-form-item label="Имя" class="label">
              <el-input v-model="registerData.name" class="input"></el-input>
            </el-form-item>
            <el-form-item label="Почта" class="label">
              <el-input v-model="registerData.email" class="input" type="email"></el-input>
            </el-form-item>
            <el-form-item label="Пароль" class="label">
              <el-input v-model="registerData.password" class="input"></el-input>
            </el-form-item>
            <el-form-item label="Повторите пароль" class="label">
              <el-input v-model="registerData.c_password" class="input"></el-input>
            </el-form-item>
        </el-form>
        <el-button type="primary" @click="submitForm('registerForm')" class="btn-reg">Зарегистрироваться</el-button>
      </div>
    </modal>
  </div>
</template>


<script>
export default {
  data: () => ({
    registerData: {
      name: '',
      email: '',
      password: '',
      c_password: ''
    }
  }),
  methods: {
    submitForm(formName) {
      const { name, email, password, c_password } = this.registerData

      this.$refs[formName].validate(async valid => {
        if(valid && name && email && password && c_password){
          if(password === c_password){
            await this.$store.dispatch('user/signUp', this.registerData)
          } else {
            this.$message.error('Пароли не совпадают')
            return false;
          }
        } else {
          this.$message.error('Заполнены не все поля');
          return false;
        }
      });
    }
  },
}
</script>

