import axios from 'axios'
import {Message} from 'element-ui'
import store from '@/store'
import router from '@/router'
import { getToken } from '@/utils/auth'
import baseHeaders from '@/utils/baseHeaders'

let requestQueue = []
let lastRequest = {}
const service = axios.create({
})

const { VUE_APP_API_PATH : apiPath } = process.env

let isPending = false

const serviceDecorator = config => new Promise(async (resolve, reject) => {
  const configSettings = { ...config }
  try {
    console.log(configSettings)
    const response = await service(configSettings)
    resolve(response)
  } catch (e){
    reject(e)
  }
})


service.interceptors.request.use(config => {
  config.headers = { ...baseHeaders }
  const token = store.getters['user/token'] || getToken()
  if (token){
    config.headers['Authorization'] = `Bearer ${token}`
  }
  return config
}, error => {
  console.log('error on request', error)
  return Promise.reject(error)
})

const refreshToken = async () => {
  return new Promise((resolve, _) => {
    store.dispatch('user/refreshToken').then(() => {
      resolve()
    }).catch(() => {
      Message.error('Ошибка авторизации, пожалуйста пройдите авторизацию')
      router.push({path: '/login'})
    })
  })
}

const resendPendingRequests = () => {
  requestQueue.forEach(async deferredRequest => {
    const { config, resolve } = deferredRequest
    try {
      const response = await service(config)
      resolve(response)
    } catch (error){
      console.log(`errResolve ${error}`)
      reject(error)
    }
  })
  requestQueue = []
}

service.interceptors.response.use(
  response => {
    lastRequest = {}
    return response
  },
  async (error, _) => {
    if (error.response && error.response.status === 401){
      if (!isPending){
        isPending = true
        refreshToken().then(() => {
          isPending = false
          resendPendingRequests()
        })
      }

      return new Promise((resolve, reject) => {
        lastRequest.resolve = resolve
        lastRequest.reject = reject
        requestQueue.push({resolve, reject, config: error.config})
      })
    } 
      return new Promise((_, reject) => {
        reject(error)
      })
    
  })

export default serviceDecorator
