import request from '@/utils/request'

import authData from '@/api/authConfig'

const { VUE_APP_AUTH_PATH } = process.env
const tokenPath = ''

export const login = (email, password) => {
  return request({
    url: '/api/login',
    method: 'get',
    params: {
      email,
      password
    }
  })
}

export const register = data =>{
  return request({
    url: '/api/register',
    method: 'post',
    data
  })
}

export const refreshToken = (refresh_token) =>{
  const params = Object.assign(authData, { grant_type: 'refresh_token', refresh_token })
  return request({
    url: `/api/${tokenPath}`,
    method: 'get',
    params
  })
}

export const logout = () => {
  return request({
    url: 'login/logout',
    method: 'post'
  })
}

export const getUserInfo = () =>{
  return request({
    url: '/api/current',
    method: 'get'
  })
}

export const getUserById = id =>{
  return request({
    url: `/api/users/${id}`,
    method: 'get'
  })
}
