<template>
  <router-view v-if="$route.params.playlistId || $route.path === '/my-music/create' "></router-view>
  <div class="my-music" v-else>
    <h3>Мои плейлисты</h3>
    <div class="playlists">
      <div class="create-playlist-container">
        <div class="create-playlist">
          <el-button icon="el-icon-plus" circle @click="$router.push('/my-music/create')"></el-button>
        </div>
        <span class="span-create">Создать новый</span>
      </div>
      <span v-if="loading">Загрузка...</span>
      <playlistItem 
        v-else 
        v-for="playlist in playlists" 
        :key="playlist.id" 
        :id="playlist.id"
        :playlist="playlist"
      />
    </div>
  </div>
</template>

<script>
import playlistItem from '@/components/PlaylistItem/index.vue'

import { getPlaylistsByUserId } from '@/api/playlists';

export default {
  data(){
    return{
      playlists: [],
      loading: false
    }
  },
  async mounted() {
    this.loading = true

    const resp = await this.$store.dispatch('user/getCurrent')
    this.userId = resp.data.id
    const response = await getPlaylistsByUserId(this.userId)
    this.playlists = response.data.data

    this.loading = false
  },
  components: {
    playlistItem
  },
  watch: {
    async '$route'() {
      this.loading = true

      const response = await getPlaylistsByUserId(this.userId)
      this.playlists = response.data.data

      this.loading = false
    } 
  }
}
</script>

<style lang="scss" scoped>
.my-music {
  width: 80%;
  margin: auto;
}

.playlists { 
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  .create-playlist-container {
    .create-playlist {
      display: flex; 
      justify-content: space-around;
      align-items: center;
      width: 150px;
      margin: 10px 10px 0 10px;
      height: 150px; 
      border: solid 1px #409EFF;

      .el-icon-plus:hover {
        color: #409EFF;
        cursor: pointer;
      }
    }

    .span-create {
      display: block;
      text-align: center;
      margin: 5px 0;
      width: 100%;
    }
  }
}

</style>