  <div class="header">
    <el-menu
      :router="true"
      :default-active="$route.path"
      class="el-menu-demo"
      mode="horizontal"
      >
      <div class="logo">
        <h3><mark>My</mark>TopMusic</h3>
      </div>
      <el-menu-item
        index="/" 
        route="/">
        Главная
      </el-menu-item>
      <el-menu-item
        index="/my-music" 
        route="/my-music"
        v-if="$store.getters['user/token']">
        Моя музыка
      </el-menu-item>
      <el-menu-item
        index="/genres" 
        route="/genres"
        v-if="$store.getters['user/isAdmin']">
        Жанры
      </el-menu-item>
      <div class="right-menu">
        <el-button @click="$modal.show('LoginModal')" v-if="!$store.getters['user/token']" type="primary">Войти</el-button>
        <el-button @click="$modal.show('RegistationModal')" v-if="!$store.getters['user/token']">Зарегистрироваться</el-button>
        <el-button @click="$store.dispatch('user/userLogout')" v-if="$store.getters['user/token']">Выйти</el-button>
      </div>
    </el-menu>
    <LoginModal/>
    <RegistationModal/>
  </div>
</template>

<script>
import { constantRouterMap } from '@/router'
import { mapGetters } from 'vuex'

import LoginModal from '@/components/modals/LoginModal'
import RegistationModal from '@/components/modals/RegistationModal'

export default {
  components: {
    LoginModal,
    RegistationModal
  },
  data: () => ({
    headerItems: [ ...constantRouterMap ],
    activeIndex: ''
  }),
  mounted (){
    this.activeIndex = this.$router.currentRoute.meta ? this.$router.currentRoute.meta.menuKey : ''
    this.$router.beforeEach((to, from, next) => {
      this.activeIndex = to.meta ? to.meta.menuKey : ''
      next()
    })
  }
}
</script>

