import request from '@/utils/request'

export const getPlaylists = () => (
  request({
    url: '/api/playlists',
    method: 'get'
  })
)

export const getPlaylistsById = id => (
  request({
    url: `/api/playlists/${id}`,
    method: 'get'
  })
)

export const getPlaylistsByUserId = id => (
  request({
    url: '/api/playlists',
    method: 'get',
    params: {
      user: id
    }
  })
)
