<template>
  <div class="playlist" v-if="$route.params.playlistId !== 'create'">
    <div class="playlist-header" v-if="playlist.id">
      <div class="playlist-img" :style="{'background-image': `url(http://symfony.dev.webant.ru/storage/${playlist.image})`}"></div>
      <div class="playlist-right-side">
        <div class="playlist-description">
          <span class="playlist-type">Альбом</span>
          <span class="playlist-heading">Название: {{ playlist.name }}</span>
          <span class="playlist-author">Автор: {{ author }}</span>
          <span class="playlist-genre">Жанр: {{ genre }}</span>
        </div>
        <div class="playlist-buttons">
          <el-button 
            @click="toggleForm = !toggleForm" 
            :icon="'el-icon-plus'" 
            type="primary"
            v-if="$store.getters['user/token']"
          ></el-button>
          <el-button 
            @click="toggleAdd = !toggleAdd" 
            :icon="toggleAdd ? 'el-icon-star-on' : 'el-icon-star-off'" 
            type="primary"
            v-if="$store.getters['user/token']"
          ></el-button>
          <el-button type="primary" icon="el-icon-share"></el-button>
        </div>
      </div>      
    </div> 
    <div class="playlist-body">
      <div class="playlist-create" v-if="toggleForm">
        <el-upload
          action="http://symfony.dev.webant.ru"
          :auto-upload="false"
          :show-file-list="false"
          :on-change="handleChangeFile">
          <el-button class="playlist-create-img-addbtn">Загрузить песню</el-button>
        </el-upload>
        <div class="playlist-create-right">
          <el-input 
            class="playlist-create-name" 
            v-model="form.name" 
            placeholder="Название песни">
          </el-input>
          <el-button type="primary" @click="handleSubmit" class="btn">Добавить песню</el-button>
        </div>
      </div>
      <div class="songs-list">
        <div class="song" v-for="song in songsList" :key="song.id">
          <span>{{ song.name }}</span>
          <audio controls>
            <source :src="`http://symfony.dev.webant.ru/storage/${song.file}`" type="audio/mpeg">
            Тег audio не поддерживается вашим браузером. 
          </audio>
        </div>
      </div>
    </div> 
  </div>
</template>

<script>
import imageNotFound from '@/assets/notImage.png';

import { getPlaylistsById } from '@/api/playlists'
import { getUserById } from '@/api/auth'
import { getGenreById, getNameFile, uploadFile } from '@/api/file'
import { getSongsList, postSong } from '@/api/songs'

export default {
  data() {
    return {
      imageNotFound,
      toggleForm: false,
      toggleAdd: false,
      playlistId: this.$route.params.playlistId || {},
      playlist: {},
      songsList: [],
      author: '',
      form: {
        name: '',
        file: ''
      }
    }
  },
  async mounted() {
    const response = await getPlaylistsById(this.playlistId)
    const resp = await getUserById(response.data.user_id)
    const genre = await getGenreById(response.data.genre_id)
    const response4 = await getSongsList({
      id: this.playlistId,
      limit: 15,
      page: 1
    })
    console.log(response4);
    this.playlist = response.data
    this.author = resp.data.name
    this.genre = genre.data.name
    this.songsList = response4.data.data
  },
  methods: {
    handleChangeFile(file) {
      this.form.file = file
    },
    async handleSubmit() {
      const { name, file } = this.form

      if(!name) {
        this.$message.error('Введите название песни')
        return false
      }

      if(!file.raw) {
        this.$message.error('Добавьте песню')
        return false
      }
    
      const formData = new FormData()
      formData.append('file', file.raw)

      const response = await getNameFile(formData, 'audiofiles')
      const response2 = await uploadFile({
        name,
        filepath: response.data.name
      },'songs')

      const response3 = await postSong(this.playlistId, {
        songId: response2.data.id
      })

      const response4 = await getSongsList({
        id: this.playlistId,
        limit: 15,
        page: 1
      })

      this.songsList = response4.data.data

      console.log(this.form);
    }
  },
  watch: {
    '$route'() {
      this.playlistId = this.$route.params.playlistId
    } 
  }
}
</script>

