  .playlist {
    width: 80%;
    margin: auto;
    
    &-header {
      width: 80%;
      display: flex;

      .playlist-img {
        width: 200px;
        height: 200px;
        background-position: center;
        background-size: cover;
      }
      
      .playlist-right-side {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        box-sizing: border-box;
        margin-left: 30px;

        .playlist-description {
          display: flex;
          flex-direction: column
        }
      }
    }
    .playlist-body {
      .playlist-create-right {
        flex-direction: row;
      }
    }
  }



  .songs-list {
    display: flex;
    flex-direction: column;

    .song {
      display: flex;
      flex-direction: column;
      margin: 10px 0;
      padding-bottom: 5px;
      border-bottom: 1px solid rgb(223, 223, 223);

      audio {
        width: 100%;
      }
    }
  }

  .btn {
    padding: 10px 20px;
    margin-left: 20px;
  }
</style>